<template>
    <nacl-page-container id="address-list" no-header>
        <!-- <nacl-header>
            收货地址
            <router-link
                slot="right-label"
                to="/address"
            >
                添加新地址
            </router-link>
        </nacl-header> -->
        <nacl-page-content>
            <ul
                v-if="listData.length !== 0"
                class="address-list"
            >
                <li v-for="item in listData" :key="item.id" class="address">
                    <nacl-flexbox
                        align="center"
                        justify="between"
                    >
                        <div class="address-img"><img src="../assets/address.png"></div>
                        <div class="text" @click="setDetail(item)">
                            <p>
                                <span class="name">{{ item.name }}</span><span>{{ item.tel }}</span>
                            </p>
                            <p class="address-text">{{ item.addressDetail }}</p>
                        </div>
                        <div class="edit">
                            <div @click="goAddress('?id='+item.id);"><img src="../assets/edit.png"></div>
                            <!-- <router-link :to="`/address?id=${item.id}`"><img src="../assets/edit.png"></router-link> -->
                        </div>
                    </nacl-flexbox>
                </li>
            </ul>
            <div v-else class="not-address">
                <div class=""><img src="../assets/not-address.png"></div>
                <p>您还没有设置收货地址哦~</p>
                <div @click="goAddress('');">
                    <nacl-button
                        type="primary"
                        radius
                        class="add"
                    >
                        去添加
                    </nacl-button>
                </div>
            </div>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import { NaclButton } from 'nacl-ui'
import { getAddressList, saveAddress } from '../services'

export default {
    name: 'address-list',
    components: { NaclButton },
    data () {
        return {
            listData: []
        }
    },
    async created () {
        this.listData = await getAddressList()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        }, 
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        async setDetail (data) {
            await saveAddress({
                ...data,
                isDefault: true
            })
            this.$toast('默认地址设置成功')
            // this.$router.history.go(-1)
            this.toGetUser();
            window.webkit.messageHandlers.goBack.postMessage('');
        },
        goAddress(param){
            this.toGetUser();
            window.webkit.messageHandlers.address.postMessage(param);
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
.address-list {
  padding: 0 34px;
  .address {
    position: relative;
    padding: 48px 34px;
    margin-top: 20px;
    box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.08);
    border-radius: 16px;
    background: #fff;
    &:first-child {
      .address-img {
        display: block;
      }
      .text {
        border: 0;
        width: auto;
      }
      .edit {
        position: absolute;
        top: 16px;
        right: 16px;
      }
    }
    .address-img {
      display: none;
      width: 60px;
      height: 62px;
      margin-right: 34px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .edit {
      width: 32px;
      height: 32px;
      margin-left: 50px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name {
      margin-right: 30px;
    }
    .text {
      flex: 1 0;
      border-right: 1px solid #e9e9e9;
    }
    .address-text {
      line-height: 1.5;
    }
  }
}
.add {
  width: 630px;
  height: 100px;
  margin: auto;
  font-size: 36px;
  color: #fff;
}
.not-address {
  text-align: center;
  img {
    width: 384px;
    height: 336px;
    margin: 56px auto 56px;
  }
  p {
    margin-bottom: 56px;
    font-size: 36px;
    color: #666;
  }
}
</style>
